









































































































































































import { AppService } from "@/service/app.service";
import { Vue, Component, Prop, Watch } from "vue-property-decorator";

import AdvertisementSquareBannerList from "@/x-vue/widgets/advertisement/AdvertisementSquareBannerList.vue";
import AdvertisementLineBanner from "@/x-vue/widgets/advertisement/AdvertisementLineBanner.vue";

import PushNotificationIcon from "@/x-vue/components/push-notification/PushNotificationIcon.vue";
import { ForumInterface, PostModel } from "@/x-vue/interfaces/forum.interface";
import { Err } from "@/x-vue/services/defines";

import ForumUserAvatar from "@/x-vue/components/forum/ForumUserAvatar.vue";
import UserMenu from "@/x-vue/components/forum/UserMenu.vue";
import { CategoryModel } from "@/x-vue/interfaces/interfaces";
import { ApiService } from "@/x-vue/services/api.service";
import store from "@/store";

@Component({
  components: {
    AdvertisementSquareBannerList,
    AdvertisementLineBanner,
    PushNotificationIcon,
    ForumUserAvatar,
    UserMenu,
  },
})
export default class extends Vue {
  // 글 읽기 아래의 글 목록 표시의경우, 글이 속성으로 전달 됨. 카테고리 구분 등에서 사용.
  @Prop({ default: () => new PostModel() }) post!: PostModel;

  app = AppService.instance;
  api = ApiService.instance;

  // 게시판 목록 객체
  forum: ForumInterface = new ForumInterface();

  // 현재 게시판 카테고리
  category: CategoryModel = new CategoryModel();

  // 총 글 수
  total = 0;

  // 총 페이지 수
  totalNoOfPages = 0;

  // 현재 페이지 번호.
  // 세번째 페이지에서 글 읽기 -> 글 보기 페이지 아래에 글 목을 보여줄 때에는 3 번째 페이지를 보여 줘야 한다.
  currentPage = 1;

  async mounted(): Promise<void> {
    this.init();
    this.initPostCount();
    this.loadPosts();
    this.initCategoryBanners();
  }

  /**
   * 각종 초기화
   *
   * - 현재 게시판 카테고리 로드
   * - 글 목록을 위한 각종 설정
   */
  async init(): Promise<void> {
    // 글 수
    this.forum.limit = 20;
    // 현재 카테고리
    this.forum.categoryId = this.categoryId;
    // 사용자 번호로 글 검색
    const userIdx = this.$route.params.userIdx;
    if (userIdx) this.forum.userIdx = parseInt(userIdx);

    // 글 목록에서, 각 글의 마지막 코멘트 1개만 가져온다.
    this.forum.comments = 1;

    /// 현재 카페의 글만 가져온다.
    /// 만약, 현재 사이트가 국가 카페가 아니거나, 국가가 없는 메인 카페이면, empty. 즉, 전체 글을 가져온다.
    this.forum.countryCode = store.state.currentCafe.countryCode;

    // 쿼리 파라메타로 페이지 번호가 넘어오면, 그 페이지를 연다.
    if (this.$route.query.page) {
      this.currentPage = this.routePageNo;
      this.forum.page = this.currentPage;
    } else {
      this.currentPage = 1;
      this.forum.page = 1;
    }

    // 서브 카테고리
    if (this.subCategory) {
      this.forum.subCategory = this.subCategory;
    }
    // console.log("init::searchOptions", this.forum.searchOptions);

    // 카테고리 로드
    try {
      this.category = await this.api.categoryGet(this.categoryId);
    } catch (e) {
      this.app.error(e);
    }
  }

  get routePageNo(): number {
    return this.$route.query.page ? parseInt(this.$route.query.page as string) : 1;
  }

  // 카테고리 아이디
  // 라우트 파라메타로 넘어온 id 또는 글의 id.
  get categoryId(): string {
    if (this.post.idx) return this.post.categoryId;
    else return this.$route.params.categoryId;
  }

  get subCategory(): string {
    return this.$route.query.sc as string;
  }

  get posts(): PostModel[] {
    return this.forum.posts;
  }

  get showBanners(): boolean {
    return this.$store.state.advertisementSettings.categoryArray?.includes(this.categoryId);
  }

  // 글 쓰기 URL
  get createLink(): string {
    return this.app.postCreateUrl({
      categoryId: this.categoryId,
      subCategory: this.subCategory,
      pageNo: this.currentPage,
    });
    // const uri = this.app.forumListUri(this.subCategory, this.currentPage);
    // return "/forum/edit/" + this.categoryId + (uri ? "?" + uri : "");
  }

  /**
   * 각 페이지로 이동하는 링크
   */
  pageLinkGenerator(pageNo: number): string {
    return this.app.postListUrl({
      categoryId: this.categoryId,
      subCategory: this.subCategory,
      pageNo: pageNo,
    });
    // let uri: string = "/forum/" + this.categoryId;
    // const pageUri = this.app.forumListUri(this.subCategory, pageNum);
    // return pageUri ? uri + "?" + pageUri : uri;
  }

  onPageChanged(page: number): void {
    // console.log("page changed", page);
    this.forum.page = page;
    this.loadPosts();
  }

  async initCategoryBanners(): Promise<void> {
    if (this.showBanners) {
      // console.log("fetching category banners");
      try {
        await this.app.loadBanners("square");
        await this.app.loadBanners("line");
        // console.log(this.$store.state.banners);
      } catch (e) {
        console.log("initCategoryBanners", e);
      }
    }
  }

  async initPostCount(): Promise<void> {
    try {
      this.total = await this.app.api.postCount(this.forum.searchOptions);
      this.totalNoOfPages = Math.ceil(this.total / this.forum.limit);
    } catch (e) {
      if (e == Err.category_not_exists) {
        console.log("@ignore category not exists error on counting no of posts.");
      } else {
        this.app.error(e);
      }
    }
  }

  @Watch("$route.query.sc")
  onQueryChange(): void {
    // return on the first load. oldsubcategory is undefine on the first load
    // console.log(subcategory, oldsubcategory);
    this.forum.subCategory = this.subCategory;
    this.initPostCount();
    this.loadPosts();
  }

  async loadPosts(): Promise<void> {
    // console.log("searchOptions;", this.forum.searchOptions);
    if (this.forum.canLoad) return;
    this.forum.loading = true;
    try {
      this.forum.posts = await this.app.api.latestPosts(this.forum.searchOptions);
      // console.log(this.forum.posts);
    } catch (error) {
      this.app.error(error);
    }
    this.forum.endLoad();
  }

  viewUrl(post: PostModel): string {
    return (post.relativeUrl || `/${post.idx}`) + location.search;
  }
  subcategoryUrl(cat: string): string {
    return this.app.postListUrl({
      categoryId: this.categoryId,
      subCategory: cat,
      pageNo: this.currentPage,
    });
  }
}
